function Skills() {
  const front = ["HTML", "CSS", "Bootstrap", "Materialize", "SASS", "Javascript", "Vanilla", "jQuery", "React", "Web APIs", "Gulp", "Responsive Design"];
  const back = ["Node.js", "express.js", "socket.io", "mocha & chai", "Database", "SQL", "Firebase"];
  const uxui = ["Wireframing", "Prototyping", "Usability", "Visual Communication"];
  return(
    <section id="skills" className="skills">
      <h1 className="h1-content">Skills</h1>
      <div className="skills-content">
        <div>
          <h2>Front-End</h2>
          {front.map( (item, index) => {
            if (item !== "Bootstrap" && item !== "Materialize" && item !== "Vanilla" && item !== "jQuery"){
              return <p key={index}>{item}</p>
            } else {
              return <small key={index} className="sub-skill">{item}</small>
            }
          })}
        </div>

        <div>
          <h2>Back-End</h2>
          {back.map( (item, index) => {
            if (item !== "Node.js" && item !== "Database"){
              return <small key={index} className="sub-skill">{item}</small>
            } else {
              return <p key={index}>{item}</p>
            }
          })}
        </div>

        <div>
          <h2>UX/UI</h2>
          {uxui.map( (item, index) => <p key={index}>{item}</p>)}
        </div>
      </div>
    </section>
  )
}

class Portfolio extends React.Component {
  constructor(props){
    super(props);
    this.handleClick = this.handleClick.bind(this);

    this.state = {
      buttons: ["E-COMMERCE", "CHAT", "SOCIAL NETWORK", "DASHBOARD", "FOODMAP", "TWITTER"]
    }
  }

  handleClick(e) {
    console.log(e.target.innerHTML)
    const img = document.querySelector("#img");
      if (e.target.innerHTML === "E-COMMERCE") {
        return img.innerHTML = `<img className="image" src={require('./assets/ecommerce.png')} />`
      } else if (e.target.innerHTML === "DASHBOARD") {
        return img.innerHTML = `<img className="image" src={require('./assets/dashboard.png')} />`
      } else if (e.target.innerHTML === "SOCIAL NETWORK") {
        return img.innerHTML = `<img className="image" src={require('./assets/social-network.png')} />`
      } else if (e.target.innerHTML === "FOODMAP") {
        return img.innerHTML = `<img className="image" src={require('./assets/foodmap.png')} />`
      } else if (e.target.innerHTML === "TWITTER") {
        return img.innerHTML = `<img className="image" src={require('./assets/twitter.png')} />`
      }
  }

  render() {
    return (
    <section id="portfolio" className="portfolio">
      <h1 className="h1-content">My works</h1>
      <img className="mockup" src={require('./assets/mockup.png')} />
      <img className="image" src={require('./assets/ecommerce.png')} />
      <div id="img"></div>
      <div className="works-options">
        {this.state.buttons.map( (item, index) => 
        <Button key={index} onClick={this.handleClick} className="works">{item}</Button> 
        )}
      </div>
    </section>
  )}
}

